import React from "react";
import { Link } from "gatsby";
import { FiGithub } from "react-icons/fi";

import Helmet from "../../components/Helmet";
import Logo from "../../components/Logo";
import isBrowser from "../../utils/isBrowser";
import * as querystring from "../../utils/querystring";

const SignInButton = ({ children, ...props }) => (
    <button css={{
        display: "flex",
        alignItems: "center",
    }} { ...props }>
        { children }
    </button>
);

const authorizeGitHubIdentity = () => {
    let url = "https://github.com/login/oauth/authorize?scope=user:email&client_id=" + process.env.GITHUB_CLIENT_ID;

    if (!isBrowser()) return;

    const qs = querystring.parse(window.location.search);
    if (qs.path) {
        url += "&redirect_uri=" + encodeURI(window.location.origin + "/authorize/github?path=" + qs.path);
    }
    window.location.href = url;
};

export default () => {
    React.useEffect(() => {
    }, []);

    return (
        <div css={{
            textAlign: "center",
        }}>
            <Helmet
                title="Authorize"
                description="Login to Glee to start using it."
            />
            <Link
                to="/"
                css={{
                    height: 42,
                    marginBottom: 25,
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    width: "100%",
                }}
            >
                <Logo width="100%" />
            </Link>
            <div>
                <SignInButton onClick={ authorizeGitHubIdentity }>
                    <FiGithub css={{ marginRight: 10 }} />
                    <div>Login with GitHub</div>
                </SignInButton>
            </div>
        </div>
    );
}
